// @flow
import type { Action, State } from './types';

let visitorReducer = function visitorReducer(state: State = null, action: Action) {
    switch (action.type) {
    case 'app/visitor/SUSCRIBE':
        return {
            email: action.email,
            emailPreference: 'SOMETIMES',
        };
    case 'app/visitor/UPDATE_EMAIL_PREFERENCE':
        if (!state) {
            return state;
        }
        return {
            ...state,
            emailPreference: action.emailPreference,
        };
    default:
        return state;
    }
};

if (IS_CLIENT) {
    const storage = require('redux-persist/es/storage').default;
    const { persistReducer } = require('redux-persist');
    visitorReducer = persistReducer(
        {
            key: 'app::visitor',
            storage,
        },
        visitorReducer,
    );
}

const immutableVisitorReducer = visitorReducer;
export default immutableVisitorReducer;



